.navbar {
  font-family: "Inter", Arial, sans-serif;
  display: flex;
  align-items: center;
  position: relative;
}

.navbar-nav.flex-row {
  display: flex;
  gap: 2rem;
}

.nav-link {
  font-size: 0.9rem;
  letter-spacing: 1px;
  color: #000 !important;
  text-decoration: none;
  position: relative;
  padding: 0.4rem 0;
}

.nav-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 0;
  height: 1px;
  background-color: #000;
  transition: width 0.25s ease;
}

.nav-link:hover::after {
  width: 100%;
}

.navbar-brand {
  font-size: 1.3rem;
  letter-spacing: 4px;
  color: #000 !important;
  text-decoration: none;
}

.navbar-center {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}

/* Mobile */
@media (max-width: 991px) {
  .navbar-center {
    justify-content: center;
    margin: 0 auto;
  }

  .navbar-nav {
    gap: 1rem !important;
  }

  .navbar-collapse {
    margin-top: 1rem;
    background: #fff;
  }
}
